export default {
    props: {
        venueData: {
            type: Object,
            require: true
        }
    },
    methods: {
        handleItemClick () {
            wx.navigateTo({
                url: `/pages/venueDetail/venueDetail?id=${this.venueData.id}`
            })
        }
    }
}
